import env from '../globals/KED.env';
import { users } from './data/users';
import { parseQueryString } from '../utils/query-string';
var username = parseQueryString(location.search).user;
if (username) {
    var user = users.find(function (u) { return u.username === username; });
    if (user) {
        env.currentUser = user;
    }
}
var _a = parseQueryString(location.search, { toLower: true }), role = _a.role, school = _a.school, schoolgrade = _a.schoolgrade;
if (env.currentUser) {
    if (role) {
        env.currentUser.roles = role.split(',');
    }
    if (school) {
        env.currentUser.school = school;
    }
    if (schoolgrade) {
        var schoolGradeInt = parseInt(schoolgrade);
        env.currentUser.schoolGrade = schoolGradeInt;
        env.currentUser.schoolType = schoolGradeInt > 9 ? "Gymnasium" : "Grundskolor";
    }
}
