import env from '../../../globals/KED.env';
import { isTeacherAtSchool } from '../../../access-control';
export function isEmployee(atSchool) {
    return atSchool ?
        isTeacherAtSchool(env.currentUser, atSchool) || env.currentUser.roles.includes("ADMIN") :
        env.currentUser.roles.some(function (role) { return role === 'EMPLOYEE' || role === 'ADMIN'; });
}
