import * as React from 'react';
export function Confirmation(props) {
    return React.createElement("div", { className: "confirmation-box " + props.className + (props.visible ? " visible" : "") },
        React.createElement("p", null, props.text),
        React.createElement("button", { onClick: function () { return props.onConfirm(); } }, "OK"),
        React.createElement("button", { onClick: function () { return props.onCancel(); } }, "Avbryt"));
}
