import cfg from '../globals/KED.cfg';
cfg.ENVIRONMENT = process.env.ENVIRONMENT;
cfg.KED_API_URL = process.env.KED_API_URL; // "https://kedbackendtest.azurewebsites.net/api/"
cfg.EDS_API_URL = process.env.EDS_API_URL; // "https://edsportalowinapi.azurewebsites.net/api/"
cfg.KED_TOKEN_URL = process.env.KED_TOKEN_URL; // "https://kedauthtest.azurewebsites.net/token"
cfg.KED_CLIENT_ID = process.env.KED_CLIENT_ID; // "devclient", "testclient", "..."
cfg.KED_CLIENT_SECRET = process.env.KED_CLIENT_SECRET;
cfg.KED_REALM = process.env.KED_REALM; // "SE1"
cfg.KED_LOCALE = cfg.KED_LOCALE || process.env.KED_LOCALE; // "sv", "en". Only set from process.env if not set from SiteVision element config.
cfg.KED_SCHOOL_LOCALE = cfg.KED_SCHOOL_LOCALE || process.env.KED_SCHOOL_LOCALE; // "sv", "en_sin", "en_nin". Only set from process.env if not set from SiteVision element config.
cfg.KED_RESOURCES_URL = cfg.KED_RESOURCES_URL || process.env.KED_RESOURCES_URL;
